body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*****************************
  NAV
*****************************/
.rev-logo {
  width: auto;
  height: 2em;
  margin-right: 2em;
}

.margin-nav {
  margin-right: 2em
}


.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
  color: #000000;
}

.navbar-expand-md .navbar-nav .nav-link {
  padding-right: .75rem;
  padding-left: .75rem;
}

.bg-light {
  background-color: #ffffff;
}

nav {
  border-bottom: 1px solid #ffffff;
  margin-bottom: 20px;
  box-shadow: 1px -12px 28px -1px rgba(0, 0, 0, 0.87);
}

/*****************************
  Login
*****************************/
.btn-login, .btn-login:hover, .btn-login:active, .btn-login:visited {
  background-color: #f16930;
  color: #ffffff;
}

.btn-register.outline, .btn-register.outline:hover, .btn-register.outline:focus, .btn-register.outline:active, .btn-register.outline.active, .open > .dropdown-toggle.btn-register {
	color: #50545d;
  border-color: #50545d;
}
.btn-register.outline:active, .btn-register.outline.active {
	border-color: #50545d;
	color: #50545d;
	box-shadow: none;
}
.inline {
  display: inline-block;
}

